import { BOOKMARK } from "../constant";

const initState = [{
    id: 0,
    title: 'MarksBox 签云',//若无可从intro中截取
    intro: 'MarksBox 签云是一款网页端的书签收藏工具，您可以用它来保存书签，记录灵感，甚至作为日常备忘录。',
    url: 'https://marksbox.github.io',//完整url
    domain: 'marksbox.github.io',//截取的域名，若无则从url中截取
    note: '#云书签 #笔记 #TODO #v1.0',//测试传参
    updatedAt: '',
    img:'/favicon.ico'
}, ] 
export default function bookmarkReducer(preState=initState,action){
    const{type, data} = action
    // console.log('reducer bookmark', action)
    switch(type){
        case BOOKMARK:
            if(!data)return initState //不想再写一个初始化数据的action，就通过不传data置空
            return data
        default:
            return preState
    }
}